import $ from 'jquery';

const Navigation = () => {
    let current = $(".nav a").position();
    let currentWidth = $(".nav a").parent("li").width();
    $(".nav .slide1").css({ left: current.left, width: currentWidth });
    const observer = new IntersectionObserver((entries) => {
        entries.forEach((entry) => {
            if (entry.isIntersecting && window.innerWidth > 800) {
                if (entry.target.classList.contains("home-container")) {
                    const homPosition = $("#h").position();
                    const homWidth = $("#h").parent().width();
                    $(".nav .slide1").css({ opacity: 1, left: homPosition.left, width: homWidth });
                } else if (entry.target.classList.contains("abt-container")) {
                    const abtPosition = $("#a").position();
                    const abtWidth = $("#a").parent().width();
                    $(".nav .slide1").css({ opacity: 1, left: abtPosition.left, width: abtWidth });
                } else if (entry.target.classList.contains("exp-container")) {
                    const expPosition = $("#e").position();
                    const expWidth = $("#e").parent().width();
                    $(".nav .slide1").css({ opacity: 1, left: expPosition.left, width: expWidth });
                } else if (entry.target.classList.contains("pro-container")) {
                    const proPosition = $("#p").position();
                    const proWidth = $("#p").parent().width();
                    $(".nav .slide1").css({ opacity: 1, left: proPosition.left, width: proWidth });
                } else if (entry.target.classList.contains("con-container")) {
                    const conPosition = $("#c").position();
                    const conWidth = $("#c").parent().width();
                    $(".nav .slide1").css({ opacity: 1, left: conPosition.left, width: conWidth });
                }
            }
        });
    }, { threshold: .5 });
    observer.observe(document.querySelector(".home-container"));
    observer.observe(document.querySelector(".abt-container"));
    observer.observe(document.querySelector(".exp-container"));
    observer.observe(document.querySelector(".pro-container"));
    observer.observe(document.querySelector(".con-container"));
    $(".nav a").on("click", function () {
        let position = $(this).position();
        let width = $(this).parent().width();
        $(".nav .slide1").css({ opacity: 1, left: + position.left, width: width });
        observer.disconnect();
        setTimeout(() => {
            observer.observe(document.querySelector(".home-container"));
            observer.observe(document.querySelector(".abt-container"));
            observer.observe(document.querySelector(".exp-container"));
            observer.observe(document.querySelector(".pro-container"));
            observer.observe(document.querySelector(".con-container"));
        }, 400);
    });
    $(".nav a").on("mouseover", function () {
        let position = $(this).position();
        let width = $(this).parent().width();
        $(".nav .slide2").css({ opacity: 1, left: + position.left, width: width });
    });
    $(".nav a").on("mouseout", function () {
        $(".nav .slide2").css({ opacity: 0 });
    });
}
export default Navigation;