const Loader = () => {
    const load = document.querySelector(".load-container");
    const nav = document.querySelector(".nav-container");
    const home = document.querySelector(".home-container");
    const abt = document.querySelector(".abt-container");
    const exp = document.querySelector(".exp-container");
    const pro = document.querySelector(".pro-container");
    const con = document.querySelector(".con-container");
    load.parentNode.removeChild(nav);
    load.parentNode.removeChild(home);
    load.parentNode.removeChild(abt);
    load.parentNode.removeChild(exp);
    load.parentNode.removeChild(pro);
    load.parentNode.removeChild(con);
    load.parentNode.appendChild(load);
    setTimeout(() => {
        load.style.opacity = "0";
        load.parentNode.appendChild(nav);
        load.parentNode.appendChild(home);
        load.parentNode.appendChild(abt);
        load.parentNode.appendChild(exp);
        load.parentNode.appendChild(pro);
        load.parentNode.appendChild(con);
        load.addEventListener("transitionend", () => {
            load.parentNode.removeChild(load);
        });
    }, 3000);
}
export default Loader;